 .sc-table td, th {  
  border: 1px solid #74756E;
  border-left: 0px;
  border-top: 0px;
  text-align: left;
}

.sc-table {
  border-collapse: collapse;
  width: 100%;
  margin-top: 25px;
}

.subtotal-title {
  font-weight: bold;
}

.row-points{
  color: #74756E;
  font-weight: normal;
  font-size: .8em;
 }

.sc-table th, td {
    padding: 15px;
    text-align: left;
    font-size: 18px;
}

.row-title {
  padding-left: 10px;
  font-weight: 400;
}

.sc-table th {
  border-left: 0px;
  border-right: 0px;
  text-transform: uppercase;
  text-align: center;
  font-weight: bold;
  font-size: 17px;
  color: #33342E;
}

.sc-table tr:hover {
  background: #F4F4F4;
}

.sc-table tr#header:hover{
  background: white;
}

.sc-table td:hover{
  background: #E0E0E0;
  cursor: pointer;
}

/* .sc-table td.row-title:hover{
  background: #F4F4F4;
} */

.expand-icon {
  display: none;
  cursor: pointer;
}

#expand-icon {
  max-width: 29px;
}

#icon-cell {
  display: none;
  height: 100%;
}

tr:hover > #icon-cell {
  background: white;
}

#biden, #warren, #sanders,
#buttigieg, #booker, #yang, #steyer {
  text-align: center;
  font-weight: bold;
  font-size: 24px;
  line-height: 27px;
  color: #33342E;
}

.subtotals td {
  border-bottom: 0px;
  text-align: left;
  cursor: auto;
  background-color: #F4F4F4;
  pointer-events: none;;
}

.subtotals > td {
  width: 11%
}

.subtotals > .subtotal-title {
  width: 55%;
}

.table-title {
  display: inline-block;
  margin-top: 1.2em;
  margin-bottom: 0;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 35px;
  color: #33342E;
}

.table-subtitle {
  display: inline-block;
  font-weight: bold;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: 0.11em;
  text-transform: uppercase;
  color: #33342E;
  margin-top: 1.3em;
  margin-bottom: 1.3em;
}

.table-description{
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  margin-top: 1em;
  color: #33342E;
  max-width: 50%;
}

.table-points{
  color: #74756E;
}

.info-icon {
  cursor: pointer;
  max-height: 21px;
  padding-right: 12px;
  margin-bottom: -4px;
}

/* Spacing specific to desktop */
@media screen and (min-width: 520px) {
  .sc-table {
    margin-top: 0px;
  }

  .table-title {
    margin-top: 89px;
  }

  #table-1 .table-title {
    margin-top: 50px;
  }

  .table-subtitle {
    margin-top: 44px;
    margin-bottom: 0;
  }
 }

@media screen and (max-width: 1200px) {
  .table-description{
    max-width: 70%;
  }
}

@media screen and (max-width: 1000px) {
  .table-description{
    max-width: 80%;
  }
}

@media screen and (max-width: 790px) {
  .table-description{
    max-width: 100%;
  }
}


@media screen and (max-width: 520px) {

  .expand-icon {
    display: inline;
    margin-left: 8px;
  }

  #biden, #warren, #sanders,
  #buttigieg, #booker, #yang, #steyer {
  font-size: 22px;
  line-height: 27px;
}

  #expand-icon:hover {
    background: #E0E0E0;
    mix-blend-mode: darken;
    border-radius: 100px;
  }

  #icon-cell {
    display: table-cell;
    vertical-align: middle;
    align-items: center;
    border: 0px;
    padding: 0;
    font-size: 24px;
    line-height: 3px;
    cursor: auto;
  }

  .table-description{
    font-size: 13px;
    line-height: 18px;
    color: #33342E;
    max-width: 100%;
  }

  .table-title, .table-subtitle {
    font-size: 18px;
    line-height: 27px;
  }

  .sc-table td {
    text-align: left;
    font-size: 10px;
    line-height: 15px;
  }

  .row-title {
    padding-left: 0;
    padding-right: 10px;
  }

  .sc-table th {
    font-size: 11px;
    padding: 8px;
    padding-top: 20px;
  }

  /* #biden, #warren, #sanders,
  #buttigieg, #booker, #yang, #steyer {
    padding: 8px;
  } */

  .info-icon {
    display: none;
  }

  .row-points{
    display: block;
    font-size: 10px;
    line-height: 15px;
   }


  .subtotal-title {
    padding-left: 10px;
  }

  .table-subtitle {
    font-size: 17px;
  }

  .table-points {
    display: none;
  }

  .subtotals > td {
    width: auto;
  }
  
  .subtotals > .subtotal-title {
    width: 55%;
  }
}


@media screen and (max-width: 380px) {

  /* .sc-table th {
    padding-left: 4px;
    padding-right: 4px;
  } */

  .expand-icon {
    margin-left: 7px;
  }
}